<template>
    <div class="freight-request">
        <adt-search-folder
            :searchRouter="{query: {accountName: search.accountName, organizationId: search.organizationId, phoneNumber: search.phoneNumber}}"
            :isStatus="true">
            <template slot="status">
                <div class="status">
                    <div class="status-left">
                        <p>货运要求状态:</p>
                        <span v-for="(status, id) in auditStatusList" :key="id" @click="auditStatus(id)" :class="{'click-status': auditStatusNum == id}">
                            {{status.status}}
                        </span>
                    </div>
                </div>
            </template>
            <template slot="always-show">
                <div class="public-transit">
                    <Form ref="formInline" :model="formInline" :rules="ruleValidate" inline label-colon>
                        <FormItem label="启运地" prop="name">
                            <Input type="text" v-model="formInline.name">
                            </Input>
                        </FormItem>
                        <FormItem label="启运地" prop="name">
                            <Input type="text" v-model="formInline.name">
                            </Input>
                        </FormItem>
                        <FormItem label="中转地" class="select-transit">
                            <Select
                                v-model="formInline.transit"
                                filterable
                                remote
                                :remote-method="transitRemote"
                                :loading="transitLoading"
                                @on-select="onChangeClick">
                                <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                            </Select>
                            <p class="select-value">
                                <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                            </p>
                        </FormItem>
                        <FormItem label="启运地" prop="name">
                            <Input type="text" v-model="formInline.name">
                            </Input>
                        </FormItem>
                    </Form>
                </div>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <AdtButton :allDelShow="true" :selectList="selectList">
                <Button>新增货运要求<Icon type="ios-alert-outline" /></Button>
            </AdtButton>
            <adt-paginator @getPages="getPages" class="pagination" :currentPage="~~page" :total="~~total" path="/authority-manage/user-manage" />
        </div>
        <div class="public-component-line"></div>
        <div class="public-list two-column">
            <Col class="public-component-card" span="12" v-for="(product, key) in listData" :key="key">
                <adt-list-item :data="product" :showCheckbox="true" @checkData="checkData">
                    <template slot="tab-title">
                        <span>货运要求编号:{{ product.productPlanNumber }}</span>
                    </template>
                    <template slot="tab-operat">
                        <span :class="{'card-status-success': product.statusCode == 'disagree', 'card-status-error': product.statusCode == 'refuse', 'card-status': product.statusCode == 'agree' }">{{ product.status }}</span>
                        <Poptip placement="right-start" class="card-icon">
                            <span>编辑</span>
                            <Icon type="md-arrow-dropdown" />
                            <div class="card-poptip-font" slot="content">
                                <div @click="edit(product)">编辑</div>
                                <div @click="detail(product)">详情</div>
                                <div @click="remove(product)" class="failure">删除</div>
                            </div>
                        </Poptip>
                    </template>
                    <template slot="tab-content">
                        <div class="tab-content">
                            <ul class="path-data">
                                <li v-for="item of list">
                                    <span>{{item}}</span>
                                    <svg class="iconfont account-tag" aria-hidden="true">
                                        <use xlink:href="#icon-andaotongcaidanICON_jiantou2"></use>
                                    </svg>
                                </li>
                            </ul>
                        </div>
                    </template>
                    <template slot="tab-bottom">
                        <div class="tab-bottom">
                            <Row>
                                <Col span="8">运输类型：集装箱</Col>
                                <Col span="8">货物类型：普货</Col>
                                <Col span="8" style="textAlign:right">服务有效期：2020/06/20</Col>
                            </Row>
                        </div>
                    </template>
                </adt-list-item>
            </Col>
        </div>
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
import AdtListItem from '@/components/adt-list-item/Index'
export default {
    mixins: [paginator],
    components: {
        AdtListItem
    },
    data() {
        return {
            formInline: {
                name: '',
                type: '',
                transit: '',
                age: ''
            },
            ruleValidate: {
                name: [
                    { required: true, message: 'The name cannot be empty', trigger: 'blur' }
                ],
                type: [
                    { required: true, message: 'The name cannot be empty', trigger: 'blur' }
                ]
            },
            listData: [
                {
                    statusCode: 'disagree',
                    status: '销售已反馈',
                    productPlanNumber: 'HDKAUYGWQD',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            city: '重庆1',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    statusCode: 'disagree',
                    status: '销售已反馈',
                    productPlanNumber: '物流方案编号3',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            city: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                }
            ],
            selectList: {},
            transitLoading: false,
            transitData: [],
            list: ['加拿大', '北级', '北京', '加州','哈尔滨', '邯郸', '韩国'],
            transitSelect: [],
            checkList: [],
            search: {},
            auditStatusNum: '默认值',
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ]
        }
    },
    methods: {
        edit(value) {},
        detail(value) {},
        remove(value) {},
        checkData (data, status) {
            if(data && status) {
                this.checkList.push(data)
            } else if (data && !status) {
                this.checkList.splice(this.checkList.indexOf(data),1)
            }
            // 选中的数据
            this.selectList = {
                params: this.checkList
            }
        },
        getPages (page) {
            this.page = page
        },
        dataInit () {
            this.getData()
        },
        getData () {},
        // 删除
        auditStatus (index) {
            // 控制状态切换传值
            this.auditStatusNum = index
        },
        onChangeClick(val) {
            if(this.transitSelect.length >= 5) {
                console.log('不能超过5个中转地')
                return
            }
            this.transitSelect.push(val.value)
        },
        transitRemote (val) {
            if (val !== '') {
                this.transitLoading = true
                setTimeout(() => {
                    this.transitLoading = false
                    const list = this.list.map(item => {
                        return {
                            value: item,
                            label: item
                        }
                    })
                    this.transitData = list.filter(item => item.label.toLowerCase().indexOf(val.toLowerCase()) > -1)
                }, 200)
            } else {
                this.transitData = []
            }
        },
        delTransit (index) {
            this.transitSelect.splice(index, 1)
        }
    }
}
</script>

<style lang="scss" scoped>
.tab-content{
    .path-data{
        padding: 6px 20px;
        overflow-x: auto;
        white-space: nowrap;
        li{
            display: inline-block;
            vertical-align: middle;
            &:last-child .account-tag{
                display: none;
            }
            &:first-child span{
                font-size: 16px;
                color: #343434;
            }
            &:last-child span{
                font-size: 16px;
                color: #343434;
            }
            span{
                vertical-align: middle;
                margin: 0 14px;
            }
            .account-tag{
                font-size: 40px;
                color: #1eafb7;
                vertical-align: middle;
            }
        }
    }
}
span{
    width: 120px;
    line-height: 30px;
}
.search-condition {
    margin-right: 30px;
    span{
        width: 200px!important;
    }
}
.min-width {
    span{
        width: 60px!important;
    }
    .ivu-input-wrapper{
        width: 60%;
    }
}
.ivu-modal-body{
    .search-condition{
        span{
            width: 180px!important;
        }
    }
}
.line{
    border:0.5px dashed $border-color;
    margin: 20px 0;
}

.search-condition{
    display: flex;
}
.modal-code{
    font-size: 14px;
    font-weight: 400;
    span{
        margin-left: 10px;
    }
}
.line-margin {
    margin-left: 30px;
}
.modal-css{
    margin-left: 0;
}
.icon-action{
    margin: 0 4px;
}
</style>
